@import './variable.scss';

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

@for $i from 1 through 12 {
  .col-xs-#{$i}, .col-sm-#{$i}, .col-md-#{$i}, .col-lg-#{$i}, .col-xl-#{$i} {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
  }
}

@for $i from 1 through 12 {
  .col-xs-#{$i} {
    flex: 0 0 #{(($i/$num-col) * 100) + '%'};
    max-width: #{(($i/$num-col) * 100) + '%'};
  }
}

@media only screen and (min-width: $sm-width) {
  @for $i from 1 through 12 {
    .col-sm-#{$i} {
      flex: 0 0 #{(($i/$num-col) * 100) + '%'};
      max-width: #{(($i/$num-col) * 100) + '%'};
    }
  }
}

@media only screen and (min-width: $md-width) {
  @for $i from 1 through 12 {
    .col-md-#{$i} {
      flex: 0 0 #{(($i/$num-col) * 100) + '%'};
      max-width: #{(($i/$num-col) * 100) + '%'};
    }
  }
}

@media only screen and (min-width: $lg-width) {
  @for $i from 1 through 12 {
    .col-lg-#{$i} {
      -webkit-box-flex: 0;
      flex: 0 0 #{(($i/$num-col) * 100) + '%'};
      max-width: #{(($i/$num-col) * 100) + '%'};
    }
  }
}

@media only screen and (min-width: $xl-width) {
  @for $i from 1 through 12 {
    .col-xl-#{$i} {
      -webkit-box-flex: 0;
      flex: 0 0 #{(($i/$num-col) * 100) + '%'};
      max-width: #{(($i/$num-col) * 100) + '%'};
    }
  }
}