@import '../../styles/variable';

.search {
  &__title {
    color: $black;
    font-size: 4rem;
    font-weight: 400;
    letter-spacing: -0.96px;
    line-height: 4.8rem;
    opacity: 0.75;
    text-align: left;
    margin-bottom: 4rem;
  }
  &__input {
    .form-element {
      &__input {
        padding-top: .5rem;
        font-size: 2.5rem;
      }
    }
  }
  &__filter {
    margin-bottom: 2rem;
  }
  &-result {
    &__searching {
      margin: 1rem 0;
      font-size: 1.2rem;
      font-weight: 400;
      letter-spacing: 0.09px;
      line-height: 1.4rem;
      color: $grey-1;
    }
  }
  &__header {
    .btn {
      transform: translateX(-1.5rem);
      [class^="icon-"] {
        font-size: 1.3rem;
      }
    }
  }
}