.btn {
  display: inline-block;
  margin: .2rem;
  padding: 1rem;
  border-radius: 3px;
  outline: none;
  font-size: 1.4rem;
  line-height: 1rem;
  background-color: transparent;
  cursor: pointer;
  transition: .3s ease-in-out;
  border: none; }
  .btn-primary {
    padding: .6rem 1rem;
    color: #ffffff;
    background-color: #784CC0;
    border: 1px solid #784CC0; }
    .btn-primary:hover {
      background-color: #976BE2; }
    .btn-primary:focus {
      background-color: #5D3999; }
  .btn-default {
    border: 1px solid #CECED2;
    color: #CECED2; }
    .btn-default:hover {
      color: #000000; }
  .btn-outline {
    border: none; }
  .btn-danger {
    color: #ffffff;
    background-color: #E54D42;
    background-color: #E54D42;
    border: 1px solid #E54D42;
    opacity: 1; }
    .btn-danger:hover {
      opacity: 0.6; }
  .btn-primary-color {
    background-color: transparent;
    color: #784CC0; }
    .btn-primary-color:hover {
      background-color: #784CC0;
      color: #ffffff; }
  .btn__title {
    display: inline-block;
    vertical-align: middle;
    line-height: 1.7rem;
    min-width: calc(100% - 4rem); }
    [class^=icon-] ~ .btn__title {
      margin-left: 1rem; }
  .btn--full-width {
    display: block;
    width: 100%; }
  .btn [class^="icon-"] {
    display: inline-block;
    vertical-align: middle;
    font-size: 2.3rem; }
  .btn .icon-heart-filled {
    color: #E54D42; }
