@import '../../styles/variable';

.form-element {
  position: relative;
  margin-top: 5px;
  margin-bottom: 5px;

  &__input {
    display: block;
    width: 100%;
    min-height: 5.6rem;
    padding: 1.5rem 5px 5px;
    border: 1px solid $grey;
    border-radius: 3px;
    opacity: 0.4;
    font-size: 1.4rem;
    color: $black;
    transition: all 0.2s ease-out;
    resize: vertical;
    outline: none;
    cursor: pointer;
    line-height: 1.9rem;
    background-color: transparent;
    text-align: left;
    &--textarea{
      padding-top: 2rem;
      overflow: hidden;
    }

    &--invalid {
      border: 2px solid $red;
    }

  }
  &__checkbox {
      position: absolute;
      opacity: 0;
      pointer-events: none;
  }

  &__input {
    height: 5.6rem;

    &:hover {
      background-color: $white;
      border: 1px solid $grey-1;
      border-radius: 3px;
      box-shadow: 0 1px 5px 0 ;
      opacity: 0.4;
    }

    &.filled ~ label, &:focus ~ label {
      top: 2.2rem;
      font-size: 1.2rem;
      transform: translateY(-100%);
    }

    &.filled {
      opacity: 0.8;
    }

    &[type="date"] {
      resize: none;
    }

    textarea & & {
      padding-top: 2.5rem;
    }
  }
  &__label {
    position: absolute;
    padding: 0 5px;
    top: 50%;
    transform: translate(0, -50%);
    left: 0;
    color: $grey;
    font-size: 1.6rem;
    cursor: pointer;

    &--checkbox {
      padding-left: 2rem;
      margin: 0 1.5rem;
      transform: translate(0);

      &::before {
        content: '';
        display: inline-block;
        width: 2rem;
        height: 2rem;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        border: 1px solid $purple;
        font-family: 'icomoon' !important;
        speak: none;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        transition: 0.2s ease-in-out;
      }
      &.checked::before {
        content: '\e902';
      }
    }
    .required {
      font-size: 1.8rem;
      padding-left: 5px;
      color: $red;
    }
  }

  .icon-dropdown {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(-50%);
  }
  &__input:focus ~ &__dropdown {
    display: block;
  }

  &__dropdown {
    display: none;
    position: absolute;
    top: 1rem;
    left: 0;
    right: 0;
    padding: .5rem;
    list-style-type: none;
    font-size: 1.6rem;
    background-color: $white;
    z-index: 1;
    border: 1px solid $grey;
    border-radius: 3px;
    transition: 0.2s ease-out; 
    box-shadow: 0 1px 5px 0 ;
    color: $black;

    li {
      padding: 0.5rem;
      cursor: pointer;

      &.active, &:hover {
        color: $white;
        background-color: $purple-1;
        box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
      }
    }
    
  }
}