@import '../../styles/variable';

.thumbnail {
  position: relative;
  overflow: hidden;
  box-shadow: 0 3rem 1.8rem -1.8rem $grey-2;
  &:hover {
    .thumbnail {
      &__img {
        transform: scale(1.03);
      }
      &__play {
        transform: scale(1.3);
      }
    }
  }
  &__img {
    background-size: 100%;
    border-radius: 3px;
    height: 0;
    padding-bottom: 56%;
    transition: all 0.3s ease-in-out;
    &--audio {
      background-image: url('/images/audio.jpg');
    }
  }
  &__play {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -1rem;
    margin-left: -1rem;
    // transform: translate(-50%, -50%);
    background-color: $white-1;
    box-shadow: 0 1px 2px 0 $black-1, inset 0 2px 0 0 $white-2;
    width: 44.1px;
    height: 44px;
    border-radius: 50%;
    color: $white-1;
    text-align: center;
    line-height: 4.4rem;
    font-size: 2.4rem;
    transition: 0.3s ease-in-out;

    span {
      color: $black;
      line-height: 2.6rem;
    }
  }
}