@import '../../styles/variable.scss';

.collection {
  &__header {
    margin-bottom: 5rem;
  }
  &__title {
    color: $black;
    font-size: 5rem;
    letter-spacing: -1.21px;
    line-height: 6rem;
    opacity: 0.30327;
    text-align: left;
  }
  &__filter {
    margin-bottom: 3rem;
  }
}

@media only screen and (min-width: $sm-width){
  .collection {
    &__header {
      .btn {
        position: absolute;
        right: 1.5rem;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
}