@import '../../styles/variable.scss';

.item {
  padding-bottom: 6.9rem;

  &__media {
    margin-bottom: 3rem ;
  }

  &__date {
    margin: 0.5rem 0;
    text-align: right;
    color: $black;
    font-size: 1.2rem;
    opacity: 0.6;
    letter-spacing: 0.09px;
    line-height: 2.1rem;
  }

  &__title {
    color: $black;
    margin: 0 0 1rem;
    font-size: 2.4rem;
    font-weight: 400;
    letter-spacing: -0.58px;
    line-height: 2.9rem;
    text-align: left;
  }

  &__description {
    max-height: 6.3rem;
    overflow: hidden;
    color: $black;
    font-size: 1.4rem;
    font-weight: 400;
    letter-spacing: 0.11px;
    line-height: 2.1rem;
    opacity: 0.6;
    text-align: left;
  }

  &__buttons {
    position: absolute;
    left: 1.5rem;
    right: 1.5rem;
    bottom: 1rem;
    font-size: 1.8rem;
    .btn {
      margin-right: 1.5rem;
    }
  }
}