@import '../../styles/variable';

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
  background-color: $black-2;
  
  &-open.modal {
    overflow-x: hidden;
    overflow-y: auto;
    display: block;
  }
  & &-dialog {
    transform: translate(-50%, -50%);
    position: absolute;
    width: 700px;
    max-width: 100%;
    top: 50%;
    left: 50%;
    padding: 2rem;
    background-color: $white;
    &--lg {
      top: 10%;
      transform: translate(-50%, 0);
    }
  }

  & &-sm {
    width: 50rem;
  }

  &-title {
    margin: 0 0.5rem 3rem;
    color: $black;
    font-size: 2.4rem;
    font-weight: 400;
    letter-spacing: -0.58px;
    line-height: 2.9rem;
    opacity: 0.75;
    text-align: left;
  }
  &-header {
    position: relative;
    .btn {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &-body {
    .form-element {
      margin-bottom: 3rem;
    }
    &__title {
      // text-align: center;
      font-size: 1.6rem;
    }
  }
}