@import '../../styles/variable';

.btn {
  display: inline-block;
  margin: .2rem;
  padding: 1rem;
  border-radius: 3px;
  outline: none;
  font-size: 1.4rem;
  line-height: 1rem;
  background-color: transparent;
  cursor: pointer;
  transition: .3s ease-in-out;
  border: none;

  // height: 44px;
  &-primary {
    padding: .6rem 1rem;
    color: $white;
    background-color: $purple;
    border: 1px solid $purple;

    &:hover {
      background-color: $purple-1;
    }
    &:focus {
      background-color: $purple-2;
    }
  }

  &-default {
    border: 1px solid $grey;
    color: $grey;

    &:hover {
      color: $black;
    }
  }

  &-outline {
    border: none;
  }

  &-danger {
    color: $white;
    background-color: $red;
    background-color: $red;
    border: 1px solid $red;
    opacity: 1;
    &:hover {
      opacity: 0.6;
    }
    // &:focus {
    //   background-color: $purple-2;
    // }
  }

  &-primary-color {
    background-color: transparent;
    color: $purple;
    &:hover {
      background-color: $purple;
      color: $white;
    }
  }

  &__title {
    display: inline-block;
    vertical-align: middle;
    line-height: 1.7rem;
    min-width: calc(100% - 4rem);
    [class^=icon-] ~ &{
      margin-left: 1rem;
    }

  }
  &--full-width {
    display: block;
    width: 100%;
  }

  & [class^="icon-"] {
    display: inline-block;
    vertical-align: middle;
    font-size: 2.3rem;
  }
  .icon-heart-filled {
    color: $red;
  }
}