.alert {
  position: fixed;
  bottom: 3rem;
  right: 0;
  z-index: 9999;
  padding: 1.5rem;
  margin-bottom: 1rem;
  //margin-right: 1rem;
  border: 1px solid transparent;
  border-radius: .25rem;
  display: inline-block;
  font-size: 1.4rem;
  transform: translateX(100%);
  transition: all .3s linear;
  //font-weight: 700;

  &--show {
    transform: translateX(0);
  }
  &--success {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
  }

  &--fail {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
  }
}