
* {
  box-sizing: border-box;
}
html {
  font-size: 10px;
}
div {
  display: block;
}

.container {
  width: 100%;
  padding-top: 100px;
  padding-right: 15px;
  padding-left: 15px;
}

.grid {
  max-width: 1280px;
  margin: 0 auto;
}

.text-right {
  text-align: right;
}
.hidden {
  display: none;
}

.justify-content-end {
  ms-flex-pack: end;
  justify-content: flex-end;
}

@import './styles/grid.scss';
@import './styles/variable.scss';
